
import React, { Component } from 'react';
import { getMovies } from '../services/fakeMovieService';
import { getGenres } from "../services/fakeGenreService";
import MoviesTable from './moviesTable.tsx';
import Pagination from '../common/pagination';
import { paginate } from "../utils/paginate";
import ListGroup from "../common/listGroup";
import { Header } from '../header/header';
import './movies.scss';
import Hr from '../common/hr';

class Movies extends Component {
    state = { 
        movies: [],
        genres: [],
        currentPage: 1,
        pageSize: 4
    }

     componentDidMount(){
         const genres = [{ name: 'All Generes'}, ...getGenres()]
         this.setState({ movies:getMovies(), genres });
     }

     handleDelete = (movie) => { 
         const movies = this.state.movies.filter(m => m._id !== movie._id);
         this.setState({ movies });
     };

     handleLike = (movie) => {  // logic for rendering and updating things on screen of liked section
         const movies = [...this.state.movies];
         const index = movies.indexOf(movie);
         movies[index] = {...movies[index]};
         movies[index].liked = !movies[index].liked;
         this.setState({ movies });
     }

     handlePageChange = page => {
         this.setState({ currentPage: page});
     }

     handleGenreSelect = genre => {
         this.setState({ selectedGenre: genre, currentPage: 1  });
     }

    render() { 
        const { length: count} = this.state.movies;
        const {pageSize, currentPage, selectedGenre, movies: allMovies} = this.state;

        if(this.state.movies.length === 0)
            return <p>There are no movies in the dataBase.</p>;
        
        const filtered = selectedGenre && selectedGenre._id ? allMovies.filter(m => m.genre._id === selectedGenre._id) : allMovies;
        const movies = paginate(filtered, currentPage, pageSize);

        return (
        <div >
            <hr></hr>
            <Header color="white" text="Displaying-Movies"/>
            <Hr/>
            <div className='full'>
                <div className="side">
                <ListGroup 
                    items={this.state.genres} 
                    selectedItem={this.state.selectedGenre}
                    onItemSelect={this.handleGenreSelect}
                />
            </div>
                <div className="tabledata">
                <p>Showing {filtered.length} movies in the database.</p>
                <MoviesTable 
                    movies={movies} 
                    onLike ={this.handleLike} 
                    onDelete={this.handleDelete} 
                />
                <Pagination 
                    itemsCount={filtered.length} 
                    pageSize={pageSize} 
                    currentPage={currentPage}
                    onPageChange={this.handlePageChange} 
                />
                </div>
            </div>
            
        </div>
        );
    }
}
 
export default Movies